<style>
.about__main {
  display: flex;
  justify-content: center;
  height: 100vh;
  margin-bottom: 100px;
  background-image: url(../assets/img/About/bg1.png);
  background-size: cover;
  background-position: center end;
  background-attachment: fixed;
  position: relative;
}

.about__main-blur {
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    #def7ff 0%,
    #f4fcff 45.64%,
    rgba(255, 255, 255, 0) 100.72%
  );
  z-index: 1;
}

.about {
  display: flex;
  max-width: 1116px;
  width: 100%;
  column-gap: 50px;
}

.about__info {
  display: flex;
  flex-direction: column;
  margin-top: 50px;
  width: 512px;
  z-index: 2;
}

.about__grid {
  display: grid;
  grid-template-columns: repeat(2, 150px);
  column-gap: 13px;
  row-gap: 13px;
}

.grid__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 150px;
  height: 130px;
  border-radius: 10px;
  border: 2px solid #f8f8f8;
  background: #fff;
  cursor: pointer;
  transition: 300ms ease;
}

.grid__item:hover {
  border: 2px solid #3685fb;
}

.grid__item h1 {
  color: #3685fb;
  text-align: center;
  font-family: "Alegreya";
  font-size: 32px;
  font-weight: 550;
  line-height: 34px;
}
.grid__item p {
  color: #002248;
  text-align: center;
  font-size: 14px;
  font-weight: 500;
  line-height: 28px; /* 200% */
}

.reacts {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 150px;
}

.reacts__info {
  display: flex;
  flex-direction: column;
  max-width: 590px;
  width: 95%;
  margin-bottom: 30px;
}

.map {
  max-width: 1180px;
  width: 100%;
  height: 550px;
  background: url(../assets/img/About/map.svg);
  background-position-y: -10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.about__btns {
  display: flex;
  height: 10px;
  column-gap: 5px;
}

.prev {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: rgb(185, 185, 185);
}

.this {
  width: 20px;
  height: 6px;
  border-radius: 10px;
  background: rgb(55, 58, 167);
}

.next {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: rgb(185, 185, 185);
}

.quote {
  max-width: 500px;
  width: 100%;
  height: 230px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #ffffff;
  padding-top: 30px;
  padding-left: 50px;
  padding-right: 50px;
  /* fill: #002248; */
  position: relative;
  margin-bottom: 15px;
  overflow: hidden;
  padding-bottom: 10px;
}

.quote__info1,
.quote__info2,
.quote__info3,
.quote__info4,
.quote__info5,
.quote__info6,
.quote__info7,
